var wxli = {
  jsapi_appid: "",
  jsapi_timestamp: "",
  jsapi_nonceStr: "",
  jsapi_signature: ""
};
export default {
  data() {
    return {
      wxli: wxli,
      headList: [],
      headSize: 1,
      tailList: [],
      tailSize: 6,
      type:"",
      photourl: ""
    };
  },
  mounted() {
    this.showDialog();
    this.getWXLi();
  },
  methods: {
    showDialog(type) {
        this.$post(URL.get_session)
					.then(data=>{
              this.type=data.object.type;
              if (type == "2") {
                this.$refs.dialog.changeShow();
              }
					})
    },
    nextUplaod() {
      if (this.headList.length == 0 || this.tailList.length < 1) {
        this.$alert.alert("请先上传照片。");
        return;
      }
  var jsonHeadStr=[];
  var jsonTailStr=[];
    for(var i=0; i<this.headList.length;i++){
          jsonHeadStr.push(this.headList[i].url)
    }
    for(var i=0; i<this.tailList.length;i++){
            jsonTailStr.push(this.tailList[i].url)
    }
      sessionStorage.setItem('headList', JSON.stringify(this.headList));
      sessionStorage.setItem('tailList', JSON.stringify(this.tailList));
      this.$router.push({
        path: "/StoreInformation",
        query: { jsonTailStr: jsonTailStr, jsonHeadStr: jsonHeadStr }
      });
    },
    getWXLi() {
        var headsession = sessionStorage.getItem('headList')
        var tailsession = sessionStorage.getItem('tailList')
        if(headsession!=null&&tailsession!=null){
          this.headList=JSON.parse(headsession)
          this.tailList=JSON.parse(tailsession)
        }
        this.$post(URL.get_js_sdk_api).then(data => {
          //处理返回的路径并且回显到页面中
          this.wxli = data.object.objects;
          this.wxConfig();
        });
    },
    wxConfig() {
      this.$wx.config({
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端star.alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: this.wxli.jsapi_appid, // 必填，公众号的唯一标识
        timestamp: this.wxli.jsapi_timestamp, // 必填，生成签名的时间戳
        nonceStr: this.wxli.jsapi_nonceStr, // 必填，生成签名的随机串
        signature: this.wxli.jsapi_signature, // 必填，签名，见附录1
        jsApiList: ["chooseImage", "uploadImage"]
      });
    },
    selectImgButtonClick(curr) {
      if (curr == "tailUrl" && this.headList.length == 0) {
        this.$alert.alert("请先上传店头照片");
        return;
      }
      this.$wx.chooseImage({
        count: 1, // 默认9
        sizeType: ["compressed"], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ["album", "camera"], // 可以指定来源是相册还是相机，默认二者都有
        success: res => {
          var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          this.uploadImage(curr, res.localIds.toString());
        }
      });
    },
    uploadImage(curr, localIds) {
      this.$wx.uploadImage({
        localId: localIds, // 需要上传的图片的本地ID，由chooseImage接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: res => {
          var serverId = res.serverId; // 返回图片的服务器端ID
          var params = new URLSearchParams();
          params.append("wxId", serverId);
          //调用vue的ajax方法
          this.$post(URL.get_wx_upload, params).then(data => {
            if (curr == "headUrl") {
              this.headList.push(data.object.url);
            }
            if (curr == "tailUrl") {
              this.tailList.push(data.object.url);
            }
          });
        }
      });
    },
    click(url) {
      this.photourl = url;
      this.$refs.imgDialog.changeShow();
    }
  }
};



